<template>
  <div>
    <h3>银联支付</h3>
    <button @click="gotoPay()">前往支付</button>
    <form
      id="submit"
      method="post"
      action="http://test.gnetpg.com:8089/GneteMerchantAPI/api/PayV36"
    >
      <input
        type="hidden"
        name="MerId"
        v-model="payParam.MerId"
      />
      <input
        type="hidden"
        name="OrderNo"
        v-model="payParam.OrderNo"
      />
      <input
        type="hidden"
        name="OrderAmount"
        v-model="payParam.OrderAmount"
      />
      <input
        type="hidden"
        name="CurrCode"
        v-model="payParam.CurrCode"
      />

      <input
        type="hidden"
        name="OrderType"
        v-model="payParam.OrderType"
      />

      <input
        type="hidden"
        name="CallBackUrl"
        v-model="payParam.CallBackUrl"
      />

      <input
        type="hidden"
        name="BankCode"
        v-model="payParam.BankCode"
      />
      <input
        type="hidden"
        name="LangType"
        v-model="payParam.LangType"
      />
      <input
        type="hidden"
        name="BuzType"
        v-model="payParam.BuzType"
      />
      <input
        type="hidden"
        name="Reserved01"
        v-model="payParam.Reserved01"
      />
      <input
        type="hidden"
        name="Reserved02"
        v-model="payParam.Reserved02"
      />
      <input
        id="SignMsg"
        type="hidden"
        name="SignMsg"
        v-model="payParam.SignMsg"
      />
    </form>
  </div>
</template>
<script>
import {
  reactive,
  toRefs,
  defineComponent,
  // computed,
  onMounted,
  nextTick,
  getCurrentInstance,
} from "vue";
import http from "../../common/http";
export default defineComponent({
  name: "Alipay",
  setup() {
    const state = reactive({
      payParam: {},
    });
    const { ctx } = getCurrentInstance();
    const gotoPay = () => {
      nextTick(() => {
        document.getElementById("submit").submit();
      });
    };
    const getPayInfo = () => {
      http
        .post("/sales-api/finance/payment/getUnionPayParameter", {
          id: ctx.$router.currentRoute.value.query.id,
        })
        .then((res) => {
          state.payParam = { ...res.data };
          gotoPay();
        });
    };
    onMounted(() => {
      getPayInfo();
    });

    return {
      ...toRefs(state),
      getPayInfo,
      gotoPay,
    };
  },
});
</script>
<style lang="scss" scoped>
</style>